html, body, .grid-container { 
  padding: 0;
  margin: 0;
  width: 50vw;
  height: 95vh;
  margin: 0 auto;
}

* {
  box-sizing: border-box;
}

.grid-container {
  display: grid;
  grid-template-columns: 5% auto 5%;
  grid-template-rows: 25% 20% auto;
  gap: 2% 2%;
  grid-template-areas: "correctGuesses playerControls timeRemaining" "correctGuesses guessInput timeRemaining" "correctGuesses numberInputs timeRemaining";
}

.timeRemaining { grid-area: timeRemaining; }

.correctGuesses { grid-area: correctGuesses; }

.playerControls { grid-area: playerControls; }

.guessInput { grid-area: guessInput; }

.numberInputs {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  gap: 1px 1px;
  grid-template-areas: "input1 input2 input3" "input4 input5 input6" "input7 input8 input9" ". input0 .";
  grid-area: numberInputs;
}

.input1 { grid-area: input1; }

.input2 { grid-area: input2; }

.input3 { grid-area: input3; }

.input4 { grid-area: input4; }

.input5 { grid-area: input5; }

.input6 { grid-area: input6; }

.input7 { grid-area: input7; }

.input8 { grid-area: input8; }

.input9 { grid-area: input9; }

.input0 { grid-area: input0; }

.thinkingBackground {
  background-image: url('/thinking_face.svg');
}

.thinkingBackground:disabled {
  background-image: url('/thinking_face-disabled.svg');
  background-size: contain;
}

.turtleBackground {
  background-image: url('/turtle.svg');
}

.turtleBackground:disabled {
  background-image: url('/turtle-disabled.svg');
  background-size: contain;
}

.playerButton {
  background-color: white;
  background-repeat: no-repeat;
  background-position: center;
  border: 0;
  height: 100%;
  width: 50%;
}

.playerControlContainer {
  height: 100%;
}

.btn{
  text-decoration: none;
  color: #668ad8;
  border-radius: 50%;
  border: solid 2px #668ad8;
  text-align: center;
  vertical-align: middle;
  overflow: hidden;
  font-weight: bold;
  transition: .4s;
  font-size: 50px;
  height: 100%;
  width: 100%;
}
.btn:disabled {
  color: #999999;
}

.guessBar {
  height: 100%;
  letter-spacing: 30px;
  font-family: 'Courier New', Courier, monospace;
  font-weight: bold;
  font-size: 50px;
  text-align: center;
  vertical-align: middle;
}

.guessCorrectComplete {
  color: #009900;
}

.guessCorrectIncomplete {
  color: #000000;
}

.guessIncorrect {
  color: red;
}

.correctGuessBar {
  height: 100%;
  width: 100%;
  border: solid 1px #009900;
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
  align-items: flex-start;
  margin:0;
}

.timeBar {
  height: 100%;
  width: 100%;
  border: solid 1px #000000;
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
  align-items: flex-start; 
  margin: 0;
}

.progress {
  width: 100%;
  margin: 0;
  max-height: 100%;
}